.note.new {
    background-color: #67d77c;
}
textarea {
    border: none;
    resize: none;
    background-color: transparent;
}
textarea:focus {
    outline: none;
}

.save {
    background-color: #e1e1e1;
    border: none;
    border-radius: 15px;
    padding: 5px 10px;
}
.save:hover {
    background-color: #ededed;
    cursor: pointer;
}